.about {
    margin: 0;
    align-items: center;
    padding-top: 120px;
    padding-bottom: 40px;
    background-color: $section-bg-color;
    background-image: url(../../src/images/about/milk.png);
    background-image: -webkit-image-set(url(../../src/images/about/milk.png) 1x,url(../../src/images/about/milk@2x.png) 2x);
    background-repeat: no-repeat;
    background-position: top 85% right 0;
    background-size: 100%;
    @media screen and (min-width: $tablet){
        background-image: none;
    }
    &__label{
        font-family: $accent-font;
        text-transform: uppercase;
        font-size: 18px;
        color: $list-color;
        text-align: center;
        @media screen and (min-width: $tablet){
            font-size: 26px;
        }
        @media screen and (min-width: $desktop){
            font-size: 30px;
        }
    }
    &__title {
        font-family: $accent-font;
        text-transform: uppercase;
        font-size: 34px;
        color: $red-color;
        text-align: center;
        margin-top: 15px;
        @media screen and (min-width: $tablet){
            font-size: 48px;
        }
        @media screen and (min-width: $desktop){
            font-size: 58px;
        }
    }
    &__thumd {
        margin-top: 25px;
        @media screen and (min-width: $tablet){
            display: flex;
            gap: 20px;
            margin-top: 50px;
        }
        @media screen and (min-width: $desktop){
            gap: 75px;
            margin-top: 55px;
            background-image: url(../../src/images/about/milk.png);
            background-image: -webkit-image-set(url(../../src/images/about/milk.png) 1x,url(../../src/images/about/milk@2x.png) 2x);
            background-repeat: no-repeat;
            background-size: 100%;
        }
    }
    &__list {
        @extend %list;
        padding-right: 30px;
        padding-left: 30px;
        padding-top: 45px;
        @media screen and (min-width: $tablet){
            max-width: 340px;
            padding-top: 100px;
            padding-left: 0;
            padding-right: 0;
        }
        @media screen and (min-width: $desktop){
            max-width: 460px;
            padding-top: 105px;
            padding-left: 0;
            padding-right: 0;
        }

    }
    &__item {
        &--first{
            color: $black-color;
            font-weight: 500;
            font-size: 12px;
            @media screen and (min-width: $tablet){
                font-size: 14px;
            }
            @media screen and (min-width: $desktop){
                font-size: 16px;
            }


        }
        &--second{
            margin-top: 30px;
            font-weight: 500;
            font-size: 12px;
            color: $text-color;
            @media screen and (min-width: $tablet){
                font-size: 14px;
            }
            @media screen and (min-width: $desktop){
                font-size: 16px;
            }

        }
        &--third{
            margin-top: 20px;
            font-weight: 500;
            font-size: 12px;
            color: $text-color;
            @media screen and (min-width: $tablet){
                font-size: 14px;
            }
            @media screen and (min-width: $desktop){
                font-size: 16px;
            }

        }

    }
    &__button{
        @include centeredFlex;
        margin-top: 25px;
        width: 176px;
        height: 44px;
        border-radius: 22px;
        border: none;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        color: $red-color;
        background-color: $white-color;
        &:hover,
        &:focus {
            background-color: #ecebeb;
            cursor: pointer;
        }

    }
    &__icon{
        display: inline-block;
        margin-left: 10px;
        width: 4px;
        height: 8px;
        stroke: $red-color;
        fill: transparent;
    }
}

.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    transition: opacity, visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &.is-hidden {
        opacity: 0;
        pointer-events: none;
        visibility: hidden;
    }
}
.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 300px;
    min-height: 200px;
    background-color: $section-bg-color;
    padding: 40px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1), scale 500ms cubic-bezier(0.4, 0, 0.2, 1);
    @media screen and (min-width: 768px) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1);
    }

    @media screen and (min-width: 1200px) {
        max-width: 528px;
        max-height: 581px;
    }
    @media screen and (min-width: $mobile) {
        max-width: 450px;
        max-height: 609px;
        position: absolute;
        left: 50%;
    }
    &__text {
        font-family: $accent-font;
        text-transform: uppercase;
        font-size: 34px;
        color: $red-color;
        text-align: center;
        margin-top: 15px;
        @media screen and (min-width: $tablet){
            font-size: 48px;
        }
        @media screen and (min-width: $desktop){
            font-size: 58px;
        }
    }
    &__close {
        position: absolute;
        top: 10px;
        right: 10px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        font-size: 20px;
        border: none;
        cursor: pointer;
        background-color: transparent;
        transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
        & svg{
        transform: scale(1.5);
        fill: $sun-color;}
    
        &:hover,
        &:focus {
            color: $red-color;
        }
    }
}
